export default {
	data() {
		return {
			xingming:'',
			zhanghao:'',
			mima:'',
			
		}
	},
	methods: {
		tianjie:function(){
			var post_xingming = this.xingming
			var post_shoujihao = this.zhanghao;
			var post_mima = this.mima;
			var myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(17[0-9]{1}))+\d{8})$/;
			if (post_shoujihao == '') {
				uni.showModal({
					title: '提示',
					content: '请输入手机号',
					showCancel: false
				});
			} else {
				if (!myreg.test(post_shoujihao)) {
					uni.showModal({
						title: '提示',
						content: '请输入正确手机号',
						showCancel: false
					});
				} else {
					if (post_mima == '') {
						uni.showModal({
							title: '提示',
							content: '请输入密码',
							showCancel: false
						});
					} else {
						if (post_xingming == '') {
							uni.showModal({
								title: '提示',
								content: '请输入姓名',
								showCancel: false
							});
						} else {
							this.post_tijiao();
						}
					}
				}
			}
		},
		post_tijiao:function(){
			var fuwuqi = getApp().globalData.quanju_fuwuqi_url;
			var guanliyuan_zhanghao = getApp().globalData.quanju_huiyuan_id
			var guanliyuan_mima = getApp().globalData.quanju_huiyuan_mima
			
			
			
			
			var shoujihao = this.zhanghao;
			var mima = this.mima;
			var xingming = this.xingming;
			
			
			var _this = this;
			uni.request({
				url: fuwuqi + 'guanliyuan_tijiao_caozuo.php', //仅为示例，并非真实接口地址。
				method: 'POST',
				data: {
					shoujihao: shoujihao,
					mima: mima,
					xingming:xingming,
					guanliyuan_zhanghao:guanliyuan_zhanghao,
					guanliyuan_mima:guanliyuan_mima,
					
					
				},
				header: {
					//'custom-header': 'hello' //自定义请求头信息
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					switch (res.data.zhuangtai) {
						case '1':
							uni.navigateBack({
								
							})
							break;
						default:
							uni.showModal({
								title: '错误',
								content: res.data.xiangqing,
								showCancel: false
							});
							break;
					}
				},
				fail() {
					uni.showModal({
						title: '错误',
						content: '请求服务器错误',
						showCancel: false
					});
				}
			});
		}
	}
}
